.notes{
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 10px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    border-radius: 10px;
    // height: 100px; 

    .heading{
        display: flex;
        flex-direction: row;
        gap: 5;
    }  
    .subhead{
        color: red;
        font-weight: 600;
        padding-left: 10px;
    }
    .title{
        font-size: 16px;
        font-weight: 600;
        color: #6440ca;
        margin-bottom: 3px;
    }
    .subtitle{
        font-size: 14px;
        font-weight: 400;
        color: #999999;
        margin-bottom: 10px;
    }
}