.recentorders {
    flex: 2;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    padding: 10px;
    .cardarea{
        background-color: #7453f8;
    }
    .carbottom{
        display: flex;
        flex-direction: column; 
        justify-content: space-between;
        .item1,.item2{
            display: flex;
            justify-content: space-between;
            .left,.right{
                .left-item{
                    display: flex;
                    align-items: center;
                    flex-direction: row;
                    gap: 7px;
                    .icon{
                        background-color: #e6f9ff;
                        width: 30px;
                        height: 30px;
                        border-radius: 20%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        color: #0ff9d2;
                        font-size: 12px;
                        
                    }
                    .details{
                        display: flex;
                        flex-direction: column;
                        .title{
                            color: #5e42d1;
                            font: bold;
                            font-weight: 600;
                            font-size: 16px;
                        }
                        .date{
                            color: #5e42d1;
                            font-size: 15px;
                            font-weight: 600;
                        }
                    }
                }
                
            }
            .right{
                
                .counter{
                    font-weight: 700;
                    font-size: 25px;
                }
            }
        }
        .item2{
            .right{
                .left-item{
                    .icon{
                        background-color: #fceeff;
                        color: #e82545;
                        font-size: 12px;
                    }
                }
                .counter{
                    color: #e82545;
                }
            }
        }

    }
}