.browser{
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 10px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    border-radius: 10px;
    height: 270px; 
    .title {
        font-weight: bold;
        font-size: 15px;
        color: #999999;
        font-weight: 700;
    }
    .border {
        border-bottom: 1px solid gray;
        margin: 10px 0;
        width: 100%;
    }

}